<template>
  <div id="order">
    <div class="list" v-if="listShow">
      <div class="top">
        <div class="title">订单管理</div>
        <div class="right">
          <el-select v-model="orderData.deliver" placeholder="未发货" class="select">
            <el-option label="未发货" value="0"></el-option>
            <el-option label="已发货" value="1"></el-option>
          </el-select>
          <el-input v-model="orderData.number" placeholder="请输入订单号" class="input"></el-input>
          <div class="btn" @click="getOrder()">筛选</div>
          <div class="btn" @click="exportOrder()">导出</div>
        </div>
      </div>
      <div class="line"></div>
      <el-table
          :data="tableData"
          class="table"
          height="800"
          style="width: 100%;">
        <el-table-column
            prop="orderId"
            label="订单号"
            align="center"
            width="80">
        </el-table-column>
        <el-table-column
            prop="productName"
            label="商品名称"
            align="center"
            width="180">
        </el-table-column>
        <el-table-column
            prop="productNumber"
            label="商品数量"
            align="center">
          <template slot-scope="scope">
            {{  scope.row.productNumber + '件' }}
          </template>
        </el-table-column>
        <el-table-column
            prop="userName"
            label="买家姓名"
            align="center">
        </el-table-column>
        <el-table-column
            prop="phone"
            label="联系方式"
            align="center"
            width="110">
        </el-table-column>
        <el-table-column
            prop="address"
            label="收货地址"
            align="center">
        </el-table-column>
        <el-table-column
            prop="orderAmout"
            label="订单总额"
            align="center">
        </el-table-column>
        <el-table-column
            prop="orderTime"
            label="下单时间"
            align="center">
        </el-table-column>
        <el-table-column
            prop="remark"
            label="备注信息"
            align="center">
          <template slot-scope="scope">
            {{  scope.row.remark?scope.row.remark: '无' }}
          </template>
        </el-table-column>
        <el-table-column
            label="发票"
            align="center">
          <template slot-scope="scope">
            <el-button @click="clickToInvoice(scope.row)" type="text" style="color: blue;" v-show="scope.row.invoicesType!=='2'">{{ scope.row.invoicesType==='3'?'普通发票':'专用发票' }}</el-button>
            <el-button type="text" style="color: gray;" v-show="scope.row.invoicesType==='2'">无</el-button>
          </template>
        </el-table-column>
        <el-table-column
            prop="status"
            label="状态"
            align="center">
          <template slot-scope="scope">
            {{  scope.row.status=== '1'?'已发货': '未发货' }}
          </template>
        </el-table-column>
        <el-table-column
            label="操作"
            align="center">
          <template slot-scope="scope">
            <el-button  type="text" style="color: gray;" v-show="scope.row.status === '1'">发货</el-button>
            <el-button @click="openMsg(scope.row)" type="text" style="color: red;"  v-show="scope.row.status === '0'">发货</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="paging">
        <div class="btn" @click="getOrderPage(1)">首页</div>
        <div class="btn" @click="getOrderPage(paging.prePage)">上一页</div>
        <div class="btn" @click="getOrderPage(paging.nextPage)">下一页</div>
        <div class="word">第{{ paging.page }}页</div>
        <div class="word">共{{ paging.pageNum }}页</div>
      </div>
    </div>
    <div class="info" v-else>
      <el-page-header @back="goBack" content="发票信息"></el-page-header>
      <table class="infoTable">
        <tr>
          <th class="titleCol left">订单号</th>
          <th>{{ invoicesData.orderId }}</th>
          <th class="titleCol">商品名称数量</th>
          <th class="right">{{ invoicesData.productName + ' * ' + invoicesData.productNumber }}</th>
        </tr>
        <tr>
          <th class="titleCol left">买家姓名</th>
          <th>{{ invoicesData.userName }}</th>
          <th class="titleCol">联系方式</th>
          <th class="right">{{ invoicesData.phone }}</th>
        </tr>
        <tr>
          <th class="titleCol left">收货地址</th>
          <th>{{ invoicesData.address }}</th>
          <th class="titleCol">订单总额</th>
          <th class="right">{{ invoicesData.orderAmout }}</th>
        </tr>
        <tr>
          <th class="titleCol left">邮箱</th>
          <th>{{ invoicesData.email }}</th>
          <th class="titleCol">下单时间</th>
          <th class="right">{{ invoicesData.orderTime }}</th>
        </tr>
        <tr>
          <th class="titleCol left">发票类型</th>
          <th>{{ type }}</th>
          <th></th>
          <th class="right"></th>
        </tr>
        <tr>
          <th class="titleCol left">发票抬头</th>
          <th>{{ invoicesOtherData.invoiceTitle }}</th>
          <th class="titleCol">开户银行</th>
          <th class="right">{{ invoicesOtherData.bank }}</th>
        </tr>
        <tr>
          <th class="titleCol left">开户银行账号</th>
          <th>{{ invoicesOtherData.accout }}</th>
          <th class="titleCol">税号</th>
          <th class="right">{{ invoicesOtherData.taxId }}</th>
        </tr>
      </table>
    </div>
    <el-dialog
        title="发货短信提示"
        :visible.sync="dialogVisible"
        width="30%"
        class="dialog"
        :lock-scroll="false"
        top="30vh"
        center>
      <div class="info">姓名： {{ rowData.userName }}</div>
      <div class="info">联系方式： {{ rowData.phone }}</div>
      <div class="info">商品： {{ rowData.productName }}</div>
      <div class="input info">
        <el-input v-model="express.id" placeholder="请输入快递单号" class="size_left"></el-input>
        <el-input v-model="express.name" placeholder="快递名称" class="size_right left"></el-input>
      </div>
      <div class="info tips">请确保信息正确无误。</div>
      <div slot="footer" class="dialog-footer">
        <div class="btn" @click="dialogVisible = false">取消</div>
        <div class="btn" @click="sendSms()">确认</div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
/*  import FileSaver from 'file-saver';
  import XLSX from 'xlsx';*/

  export default {
    name: "Order",
    data() {
      return {
        listShow: true,
        orderData: {
          deliver: '',
          number: ''
        },
        tableData: [],
        exportData: [],
        paging: {
          page: 1,
          pageSize: 5,
          pageNum: '',  //总页数
          hasNextPage: '',
          hasPreviousPage: '',  //是否有前一页后一页
          isFirstPage: '',
          isLastPage: '',  //是不是首页和尾页
          nextPage: '',
          prePage: ''  //前一页和后一页的页码
        },
        dialogVisible: false,
        rowData: {},
        express: {
          id: '',
          name: ''
        },
        invoicesData: {},
        invoicesOtherData: {},
        type: '',
        token: ''
      }
    },
    methods: {
      openMsg(row) {
        this.rowData = row;
        this.dialogVisible = true;
      },
      clickToInvoice(row) {
        this.invoicesData = row;
        //判断发票类型
        if(row.invoicesType === '3') {
          this.type = '普通发票';
        }else {
          this.type = '专用发票';
        }
        this.$axios.get('/orders/invoices',{
          params: {
            orderId: row.orderId
          }
        }).then((res) => {
          if(res.status === 200) {
            this.invoicesOtherData = res.data;
            this.listShow = false;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      goBack() {
        this.listShow = true;
      },
      getOrder() {
        this.$axios.get('/orders/orders',{
          params: {
            orderId: this.orderData.number,
            page: this.paging.page,
            pageSize: this.paging.pageSize,
            status: this.orderData.deliver
          }
        }).then((res) => {
          let data = res.data;
          if(res.status === 200) {
            this.tableData = data.list;
            this.paging.page = data.pageNum;
            this.paging.pageNum = data.pages;
            this.paging.hasPreviousPage = data.hasPreviousPage;
            this.paging.hasNextPage = data.hasNextPage;
            this.paging.isFirstPage = data.isFirstPage;
            this.paging.isLastPage = data.isLastPage;
            this.paging.prePage = data.prePage;
            this.paging.nextPage = data.nextPage;
            for(let i=0; i<data.list.length; i++) {
              this.tableData[i].orderAmout = this.tableData[i].orderAmout/100;
            }
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getOrderPage(page) {
        if(page === 0 && this.paging.isFirstPage) {
          this.$message.warning('这是已经是首页了');
        } else if(page === 0 && this.paging.isLastPage) {
          this.$message.warning('这是已经是尾页了');
        } else if(this.paging.hasPreviousPage || this.paging.hasNextPage || page === 1) {
          this.$axios.get('/orders/orders',{
            params: {
              orderId: this.orderData.number,
              page: page,
              pageSize: this.paging.pageSize,
              status: this.orderData.deliver
            }
          }).then((res) => {
            let data = res.data;
            if(res.status === 200) {
              this.tableData = data.list;
              this.paging.page = page;
              this.paging.page = data.pageNum;
              this.paging.pageNum = data.pages;
              this.paging.hasPreviousPage = data.hasPreviousPage;
              this.paging.hasNextPage = data.hasNextPage;
              this.paging.isFirstPage = data.isFirstPage;
              this.paging.isLastPage = data.isLastPage;
              this.paging.prePage = data.prePage;
              this.paging.nextPage = data.nextPage;
            }
            else {
              this.$message.error(res.msg);
            }
          }).catch((err) => {
            console.log(err);
          })
        }
      },
      exportOrder() {
        let url = 'https://acfly.cn/background/orders/exportOrders';
        let xhr = new XMLHttpRequest();
        xhr.open("get", url, true);
        xhr.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
        xhr.setRequestHeader("token", this.token);
        xhr.send();
        xhr.responseType = "blob";
        xhr.onload = function () {
          if (this.status === 200) {
            var blob = this.response;
            let href = window.URL.createObjectURL(blob);
            let link = document.createElement("a");
            link.href = href;
            link.download = "订单记录表";
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            window.URL.revokeObjectURL(href);
          }
          else {
            alert("出现了未知的错误!");
          }
        }
      },
      sendSms() {
        this.$axios.post('/sms/sms2',{
            deliveryId: this.express.id,
            deliveryName: this.express.name,
            phone: this.rowData.phone,
            productName: this.rowData.productName
        }).then((res) => {
          if(res.status === 200) {
            this.dialogVisible = false;
            this.clearOrderData();
            this.checkStatus(this.rowData.orderId, this.rowData.productName);
            this.$message.success(res.data);
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      //修改订单状态
      checkStatus(id, name) {
        this.$axios.post('/orders/updateOrder',{
          orderId: id,
          productName: name
        }).then((res) => {
          console.log(res);
          if(res.status === 200) {
            this.getOrder();
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      clearOrderData() {
        this.orderData.deliver = '';
        this.orderData.number = '';
      }
    },
    created() {
      this.token = localStorage.getItem("token");
    },
    mounted() {
      this.clearOrderData();
      this.listShow = true;
      this.getOrder();
    },
    beforeRouteEnter(to,from,next) {
      next(vm => {
        // 通过 `vm` 访问组件实例
        vm.getOrder();
      })
    },
    beforeRouteLeave(to,from,next) {
      this.clearOrderData();
      this.listShow = true;
      next();
    }
  }
</script>

<style lang="less" scoped>
  @import "../../assets/style/order/order";
</style>

